.quad-auto-nav {
    background-color: var(--hgob20a);
}

.quad-auto-nav,
.button {
    h2 {
        border-top-right-radius: 4px;
        border-top-left-radius: 4px;
        padding: 1rem 5% .5rem;
        margin-bottom: 0;
    }
    h3 {
        font-size: 1.3rem;
        padding: .5rem 5%;
        margin: 0;
    }
    ul {
        padding: 0;
        border-bottom-right-radius: 4px;
        border-bottom-left-radius: 4px;
        list-style: none;
        margin: 0;
        li {
            padding: 0;
            &:last-of-type {
                border-bottom: 0;
            }
            a {
                display: block;
                padding: 1.25em 5% 1.25em 7%;
                text-decoration: none;
                line-height: 1;
                font-weight: 400;
                font-size: 1rem;
                &:hover,
                &:focus {
                    background: var(--wgo20a);
                    color: var(--ssgr);
                    text-decoration: underline;
                }
            }
        }
    }
    .box-auto-nav {
        h2 {
            background: var(--hgob);
            color: var(--brzdk);
        }
        h3 {
            background: var(--hgo);
            color: var(--brzdk);
        }
        ul {
            background: #fff;
            color: #333;
            &:last-of-type {
                border-bottom: 2px solid var(--hgob);
            }
            li {
                border-bottom: 2px solid var(--hgob);
                a {
                    background: #fff;
                    color: var(--stgr);
                    &:hover,
                    &:focus {
                        background: var(--hgob40a);
                        color: var(--ssgr);
                        text-decoration: underline;
                    }
                }
            }
        }
    }
    .side-nav {
        h2 {
            background: #fff;
            background: var(--wgo20a);
            color: var(--brzdk);
            border: 2px solid var(--wgo);
            border-bottom: 0;
        }
        h3 {
            background: var(--wgolt);
            color: var(--brzdk);
        }
        ul {
            background: #fff;
            color: #333;
            &:last-of-type {
                border-bottom: 2px solid var(--wgo);
            }
            li {
                border: 2px solid var(--wgo);
                border-bottom: 0;
                a {
                    background: #fff;
                    color: var(--stgr);
                    &:hover,
                    &:focus {
                        background: var(--wgo40a);
                        color: var(--ssgr);
                        text-decoration: underline;
                    }
                }
            }
        }
    }
}